/**
 *
 * @param value bigint or number
 * @param byteLength number
 * @returns Buffer
 */

export function writeUIntLE(value, byteLength) {
  if (BigInt(value) < BigInt(0)) {
    throw new Error('Cannot write negative values.');
  }

  return writeIntLE(value, byteLength);
}
/**
 *
 * @param value bigint | number
 * @param byteLength number
 * @returns Buffer
 */

export function writeIntLE(value, byteLength) {
  value = BigInt(value);
  var pipe = new Pipe();
  var i = 0;
  var mul = BigInt(256);
  var sub = BigInt(0);
  var byte = Number(value % mul);
  pipe.write([byte]);

  while (++i < byteLength) {
    if (value < 0 && sub === BigInt(0) && byte !== 0) {
      sub = BigInt(1);
    }

    byte = Number((value / mul - sub) % BigInt(256));
    pipe.write([byte]);
    mul *= BigInt(256);
  }

  return new Buffer(pipe.buffer);
}
/**
 *
 * @param pipe Pipe from buffer-pipe
 * @param byteLength number
 * @returns bigint
 */

export function readUIntLE(pipe, byteLength) {
  var val = BigInt(safeRead(pipe, 1)[0]);
  var mul = BigInt(1);
  var i = 0;

  while (++i < byteLength) {
    mul *= BigInt(256);
    var byte = BigInt(safeRead(pipe, 1)[0]);
    val = val + mul * byte;
  }

  return val;
}
/**
 *
 * @param pipe Pipe from buffer-pipe
 * @param byteLength number
 * @returns bigint
 */

export function readIntLE(pipe, byteLength) {
  var val = readUIntLE(pipe, byteLength);
  var mul = Math.pow(BigInt(2), BigInt(8) * BigInt(byteLength - 1) + BigInt(7));

  if (val >= mul) {
    val -= mul * BigInt(2);
  }

  return val;
}